.ml-services {
	padding-bottom: 40px;
	@include el('container') {
		padding-left: 40px;
	}
	@include el('title') {
		font-weight: 700;
		font-family: #{$titleFont};
		@include md('style2') {
			color: #fff;
			padding: 20px 30px;
			margin-bottom: 0px;
		}
		@include md('pt-25') {
			padding-top: 25px;
		}
		@include md('pt-40') {
			padding-top: 40px;
		}
	}
	@include md('hoverline') {
		li {
			&:hover a {
				&:after {
					height: 50%;
					width: 1px;
					background: #{$templateColor};
					position: absolute;
					content: '';
					left: -30px;
				}
			}
		}
	}
	@include md('pl-30') {padding-left: 30px;}
	@include el('item') {
		position: relative;
		padding-top: 10px;
		padding-bottom: 10px;
		font-family: #{$titleFont};
		a {color: #{$textColor};}
		&:hover a {
			color: #{$templateColor};
		}
	}
	@include el('text') {
		color: #{$textColor};
		@include md('mt-20') {margin-top: 20px;}
	}
	@include md('style1') {
		@include max_desktop {
			padding-bottom: 0px;
		}
		li {
			padding-top: 5px;
			padding-bottom: 5px;
			&:before {
				padding-right: 5px;
				font-family: 'icomoon';
				content: "\e901";
				color: #66cc99;
			}
			a {
				color: #66cc99;
				font-size: 13px;
			}
		}
	}
	@include md('style2') {
		padding-bottom: 0px;
		@include max_desktop {
			padding-bottom: 0px;
		}
		li {
			position: relative;
			padding: 10px 30px;
			box-shadow: 1px 1px 1px rgba(255,255,255,0.2);
			&:first-of-type {
				box-shadow: 0px 0px 0px 1px rgba(255,255,255,0.2);
			}
			&:hover {
				a {
					color: #fff;
					&:after {right: 10px;}
				}
			}
			a {
				color: #fff;
				font-size: 13px;
				display: block;
				&:after {
					transition: all 0.5s ease;
					padding-right: 5px;
					font-family: 'icomoon';
					content: "\e901";
					color: #fff;
					font-size: 15px;
					position: absolute;
					right: 15px;
				}
			}
		}
	}
	@include md('style3') {
		.ml-services__item {
			padding-top: 5px;
			padding-bottom: 5px;
		}
	}
}
