.ml-featuredBox {
	padding: 45px 55px;
	background: #fff;
	@include el('item') {
		margin-bottom: 20px;
	}
	@include el('nr') {
		color: #{$templateColor};
		border: 2px solid #e8e8e8;
		display: inline-block;
		border-radius: 50%;
		padding: 4px 10px;
		font-size: 10px;
		margin-bottom: 10px;
	}
	@include el('text') {
		font-size: 14px;
		font-weight: 700;
		line-height: 20px;
		font-family: #{$titleFont};
		text-transform: uppercase;
		color: #000;
	}
	@include el('category') {
		font-size: 10px;
		text-transform: uppercase;
		color: #{$servicesTextColor};
	}
}
