.ml-slider {
	max-height: 800px;
	height: 800px;
	position: relative;
	@include el('caption') {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-115%, -50%);
		@include tablet_landscape {transform:  translate(-100%, -50%);}
		@include tablet_portrait {
			width: 300px;
			transform: translate(-74%, -50%);
		}
		@include mobile {
			width: 300px;
			transform: translate(-50%, -50%);
		}
		h1 {
			color: #{$sliderTextColor};
			@include desktop {font-size: 40px;}
			@include tablet_landscape {font-size: 35px; padding-bottom: 5px;}
			@include tablet_portrait {font-size: 30px; padding-bottom: 0px;}
			@include mobile {
				font-size: 26px;
				padding-bottom: 0px;
				margin-bottom: 0px;
				margin-top: 0px;
			}
		}
		h3 {
			font-weight: 400;
			font-size: 20px;
			color: #{$textColor};
			padding-bottom: 25px;
			margin-left: 5px;
			@include desktop {font-size: 17px;}
			@include tablet_landscape {font-size: 15px; padding-bottom: 15px;}
			@include tablet_portrait {font-size: 12px; padding-bottom: 10px;}
			@include mobile {
				font-size: 11px;
				margin-top: 0px;
				padding-bottom: 0px;
			}
		}
		.ml-btn {
			margin-left: 5px;
			@include mobile {font-size: 10px;}
		}
	}
	@include el('item') {
		&Bg {
			width: 100%;
			height: 100%;
			position: absolute;
			background-size: cover;
			background-position: center;
		}
	}
	@include mobile {max-height: 300px;}
	.slick-list {height: 100%;}
	.slick-track {height: 100%;}
	.slick-arrow {
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		cursor: pointer;
		outline: none;
		font-size: 50px;
		z-index: 2;
		color: #fff;
		@include mobile {font-size: 40px;}
	}
	.ml-icon-slider-prev {
		left: 10px;
		@include max_tablet {left: 0px;}
	}
	.ml-icon-slider-next {
		right: 10px;
		@include max_tablet {right: 0px;}
	}
}
