.ml-accordion {
	position: relative;
	border: 1px solid #E3E3E3;

	@include md('mb-30') {
		margin-bottom: 30px;
	}
	@include el('bgImg'){
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-repeat: no-repeat;
		background-size: cover;
		// accordion bg images
		&1 {background-image: url("../images/acc1.jpg");}
		&2 {background-image: url("../images/acc1.jpg");}
		&3 {background-image: url("../images/acc1.jpg");}
		&4 {background-image: url("../images/clinic1.jpg");}
		&5 {background-image: url("../images/clinic2.jpg");}
		&6 {background-image: url("../images/clinic3.jpg");}
		&7 {background-image: url("../images/clinic4.jpg");}

	}
	@include el('group'){
		min-height: 60px;
		margin-bottom: 0;
		border: 0;
		border-bottom: 1px solid #E3E3E3;
		background-color: transparent;
		padding: 0;
		box-shadow: none;
		margin: 0;
		border-radius: 3px;
		&.is-opened {
			.ml-accordion__title {
				display: block;
				opacity: 0;
				&:after {
					top: 50%;
					transform: rotate(180deg);
				}
				&:before {
					top: 50%;
					transform: rotate(90deg);
				}
			 }
			.ml-accordion__content {
				display: block;
				opacity: 1;
			}
			.ml-accordion__caption {
				opacity: 1;
			}
		}
	}
	@include el('caption') {
		opacity: 0;
		transition: opacity .5s;
		position: relative;
		z-index: 2;
		padding: 58px 30px;
		@include md('title') {
			padding: 0 30px;
			position: absolute;
			bottom: 0px;
		}
		@include md('style2') {padding: 28px 30px;}
	}
	@include el('list') {
		color: #fff;
		padding-bottom: 10px;
	}
	@include el('close') {
		padding-top: 10px;
		padding-bottom: 10px;
		right: 25px;
		top: 6%;
		position: absolute;
		z-index: 9;
		cursor: pointer;
		&:after {
			transform: rotate(0deg);
			content: ' ';
			width: 18px;
			height: 2px;
			display: block;
			background: #fff;
			transition: all .3s ease-in-out;
		}
	}
	@include el('title') {
		cursor: pointer;
		color: #fff;
		background-color: #{$templateColor};
		display: block;
		width: 100%;
		text-align: left;
		padding: 15px 60px 15px 25px;
		font-size: 18px;
		font-weight: 400;
		position: absolute;
		z-index: 10;
		opacity: 1;
		transition: all .4s;
		@include max_ldesktop{
			font-size: 16px;
			padding: 15px 63px 17px 25px;
		}
		&:after, &:before {
			position: absolute;
			content: ' ';
			width: 18px;
			height: 2px;
			display: block;
			background: #ddd;
			right: 25px;
			top: 50%;
		}
		&:after {
			transform: rotate(180deg);
		}
		&:before {
			transform: rotate(270deg);
		}
		&:hover {
			background: #{$blue};
			color: #fff;
		}
		@include md('mdBlue') {
			background: #{$mediumBlue};
			&:hover {
				background: #{$mediumBlueHover};
			}
		}
		@include md('mdTc2') {
			background: #{$templateColor2};
			&:hover {
				background: #fb5050;
			}
		}
	}
	@include el('content') {
		position: relative;
		display: none;
		min-height: 58px;
		height: 295px;
		@include max_ldesktop{height: 300px;}
		h4 {
			font-size: 18px;
			line-height: 20px;
			font-weight: 700;
			font-style: normal;
			margin-bottom: 20px;
			color: #fff;
		}
		p { line-height: 1.7; color: #fff;}
	}
}
