/*** Default styles ***/

// page wrapper
#page_wrapper {
	background-color: #ffffff;
	background-repeat: repeat;
	background-position: left top;
	background-attachment: scroll;
	position: relative;
	overflow: hidden;
	min-height: 100vh;
	z-index: 0;
}
// images
// responsive images
img {width: 100%; height: auto;}
.ml-bg-img {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: auto;
	background-attachment: scroll;
}

// CSS Basic Elements

// default theme font
body {
	line-height: 1.7;
	min-height: 100%;
	font-family: #{$defaultFont};
}

// Typography
// headings
h1 {
	font-size: 60px;
	line-height: 35px;
	font-weight: 300;
	padding-bottom: 20px;
}
h2 {
	font-size: 34px;
	font-weight: 700;
}
h3 {
	font-size: 24px;
	line-height: 24px;
	font-weight: 700;
}
h1,h2,h3,h4,h5,h6 {
	color: #{$titleColor};
	font-family: #{$titleFont};
}
// paragraph
p {
	font-size: 14px;
	line-height: 24px;
	font-weight: 400;
	font-style: normal;
	margin: 0 0 10px;
}
// lists
ul {
	list-style-type: none;
	padding-left: 0px;
	margin-bottom: 0px;
}

// links
a {color: #{$LinksColor}; transition: all 0.4s ease-in-out;}
a:hover {text-decoration: none; color: #{$templateColor};}
a:link {text-decoration: none;}
a:focus {outline: none;}

// button styles
.ml-btn {
	background: #{$templateColor};
	color: #fff;
	padding: 10px 15px;
	border: none;
	transition: background-color .4s ease-out;
	font-family: #{$titleFont};
	&:hover {
		background: #{$blue};
		color: #fff;
	}
	&Slider {
		height: 50px;
		display: inline-block;
		background: #{$templateColor};
		color: #fff;
		width: 45%;
		text-align: center;
		padding-top: 12px;
		vertical-align: middle;
		font-size: 15px;
		margin: 0 5px;
		@include max_desktop {
			font-size: 12px;
			width: 45%;
			padding-top: 14px;
		}
		@include tablet_portrait {
			height: 30px;
			padding-top: 5px;
		}
		@include mobile {
			font-size: 10px;
			height: 30px;
			padding-top: 6px;
			width: 45%;
		}
		@include md('pr-10') {
			margin-right: 10px;
		}
		&:hover {
			background: #{$blue};
			color: #fff;
		}
		&:focus {color: #fff;}
	}
	&:focus {color: #fff;}
	@include md('right') {float: right;}
	@include md('rounded') {border-radius: 3px;}
	@include md('block') {width: 100%; display: block;}
	@include md('inlineb') {display: inline-block;}
	@include md('bordered') {border: 1px solid #ccc;}
	@include md('style1') {
		border: 1px solid rgba(255, 255, 255, 0.5);
		transition: all .4s;
		&:hover {
			background: #{$templateColor};
			border: 1px solid rgba(255, 255, 255, 0.1);
		}
	}
	@include md('style2') {
		padding: 6px 15px;
		margin-top: 5px;
	}
	@include md('style3') {
		background: transparent;
		padding: 6px 15px;
		border: 2px solid #fff;
		&:hover {background: rgba(255, 255, 255, 0.1);}
		margin-bottom: 10px;
		margin-top: 10px;
	}
	@include md('style4') {
		border: 2px solid #fff;
		&:hover {
			background: #{$hoverColor3};
			color: #fff;
		}
	}
	@include md('style5') {
		background: #fff;
		color: #{$templateColor};
		transition: all .4s ease-out;
		&:hover {
			background: #fff;
			color: #{$blue};
		}
		&:focus {color: #{$blue}; outline: none;}
	}
	@include md('style6') {
		background: #a7eda7;
		color: #fff;
		height: 100px;
		width: 100px;
		border-radius: 50%;
		display: block;
		font-size: 11px;
		text-align: center;
		font-weight: 700;
		padding: 33px 20px;
	}
	@include md('style7') {
		background: transparent;
		color: #000;
		margin-left: 5px;
		padding: 10px 12px;
		transition: all .3s;
		&:hover {
			background: transparent;
			color: #{$templateColor3};
		}
		&:focus {
			outline: none;
			color: #000;
		}
		span {font-size: 18px;}
	}
	@include md('style8') {
		font-size: 11px;
		background: transparent;
		padding: 8px 20px;
		border: 2px solid #fff;
		&:hover {background: rgba(255, 255, 255, 0.1);}
		margin-bottom: 10px;
		margin-top: 10px;
		display: inline-block;
	}
	@include md('mdBlue') {
		background: #{$mediumBlue};
		&:hover {
			background: #{$mediumBlueHover};
		}
	}
	@include md('lg') {padding: 10px 50px;}
	@include md('white') {
		background: #fff;
		padding: 8px 15px;
		border: 2px solid #9b9b9b;
		color: #000;
		box-sizing: border-box;
		&:hover {
			background: #fbfbfb;
			color: #000;
		}
		&:focus {color: #000;}
	}
	@include md('white2') {
		background: #fff;
		border: 1px solid #e5e5e5;
		color: #{$textColor};
		&:hover {
			background: #fbfbfb;
			color: #{$textColor};
		}
		&:focus {
			background: #fff;
			color: #{$textColor};
		}
	}
	@include md('full') {width: 100%;}
	@include md('mt-20') {margin-top: 20px;}
	@include md('mt-30') {margin-top: 30px;}
	@include md('mt-45') {margin-top: 45px;}
}

// tables
table td, th{padding: 10px; border: 1px solid #{$elementsTextColor};}
// middle lines

.ml-middleLines {
	overflow: hidden;
	.ml-boxes {
		&:after, &:before {
			content:  '';
			position:  absolute;
			background:  #ddd;
			z-index:  1;
			@include tablet_landscape {display: none;}
		}
		&:after {
			bottom: -1px;
			left: 0;
			width: 100%;
			height: 1px;
		}
		&:before {
			width: 1px;
			height: 100%;
			right: -1px;
			top: 0;
		}
	}
}

// elements colors
.ml-elem--light {color: #fff;}

.ml-separator {
	@include md('style1') {
		background: #d2d2d2;
		opacity: .2;
		height: 2px;
		width: 100%;
	}
	@include md('style2') {
		background: #ccc;
		opacity: .2;
		height: 2px;
		width: 100%;
	}
	@include md('style3') {
		margin-top: 20px;
		margin-bottom: 20px;
		background: #ccc;
		opacity: .5;
		height: 1px;
		width: 100%;
	}
	@include md('space') {
		margin-top: 40px;
		margin-bottom: 30px;
	}
}
.ml-spacer-15 {height: 15px;}
.ml-spacer-25 {height: 25px;}

// hidden classes
.ml-hidden-xs {@include mobile {display: none;}}
.ml-hidden-sm {@include tablet_portrait{display: none;}}
.ml-hidden-md {@include desktop{display: none;}}
.ml-hidden-lg {@include min_ldesktop{display: none;}}
.ml-hidden-maxlD {@include max_ldesktop{display: none;}}
.ml-clearfix {display: none;}
.ml-hidden-tablet {@include  max_tablet {display: none;}}

// background
.ml-bg--dark {background: #333;}
.ml-bg--light {background: #f3f3f3;}
.ml-bg--templateColor {background: #{$templateColor};}

.ml-bg-overlay {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: 2;
	@include md('style1') {
		background: #333;
		opacity: .2;
	}
	@include md('style2') {
		background: #333;
		opacity: .1;
	}
}

// containers
.ml-container {
	margin: auto;
	@include min_tablet {width: 750px;}
	@include min_desktop {width: 970px;}
	@include min_ldesktop {width: 1170px;}
	@include md('space-between') {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	@include md('flex') {
		position: relative;
		display: flex;
		align-items: center;
	}
	@include md('fx-column') {
		@include mobile {
			flex-direction: column;
		}
	}
}
.ml-container-full {
	width: 100%;
	padding-left: 0px;
	padding-right: 0px;
}
.ml-header-containerFull {
	@include max_desktop {width: 100%;}
}

.ml-section {position: relative;}
.ml-singleSection {
	@extend .ml-section;
	padding-top: 40px;
	background: #fff;
}
.row {
	&.ml-gutter-0 {
		margin-right: 0;
		margin-left: 0;
		>[class*=col-] {
			padding-right: 0;
			padding-left: 0;
		}
	}
}

// breadcrumbs
.ml-breadcrumbs {
	border: 1px solid #e5e5e5;
	padding: 15px 30px;
	font-size: 11px;
	font-weight: 700;
	margin-bottom: 40px;
	@include el('item') {
		display: inline-block;
	}
	@include el('icon') {
		font-size: 20px;
		vertical-align: sub;
		&.ml-icon-arrow-right {
			color: #ccc;
		}
	}
	@include md('rounded') {border-radius: 3px;}
}

// text alignment
.ml-text--left {text-align: left;}
.ml-text--right {text-align: right;}
.ml-text--center {text-align: center;}

// elements position
.ml-elem--center {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

// bottom mask
.ml-bottom-mask {
	height: 57px;
	bottom: -1px;
	z-index: 2;
	position: absolute;
	width: 100%;
	left: 0;
	pointer-events: none;
}
.ml-svgmask {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}
