.ml-socialIcons {
	$si: &;
	padding-left: 2px;
	@include md('centerFlex') {
		display: flex;
		align-items: center;
		justify-content: space-evenly;
	}
	@include md('pb-30') {padding-bottom: 30px;}
	@include el('item') {
		padding: 0 6px;
		cursor: pointer;
		font-size: 16px;
		&:first-of-type {padding-left: 0px;}
		&.ml-icon-instagram { font-size: 17px;}
		@include md('light') {
			color: #fff;
			opacity: .5;
			transition: all .5s;
			&:hover {opacity: .8;}
		}
	}
	@include md('style2') {
		#{$si}__item {
			font-size: 17px;
			padding-left: 22px;
			padding-right: 22px;
			transition: all .5s;
			&:hover {color: #{$templateColor};}
		}
	}
	@include md('style3') {
		padding-top: 5px;
		padding-bottom: 5px;
		display: inline-block;
		#{$si}__item {
			font-size: 15px;
			padding-left: 5px;
			padding-right: 5px;
			transition: all .5s;
			color: #ccc;
			&:hover {color: #000;}
		}
	}
	@include md('pd-first0') {
		#{$si}__item:first-of-type {
			padding-left: 0px;
		}
	}
	@include md('size14') {
		#{$si}__item {
			font-size: 14px;
			padding-left: 15px;
			padding-right: 15px;
		}
	}
	@include md('size17') {
		#{$si}__item {
			font-size: 17px;
			padding-left: 7px;
			padding-right: 7px;
		}
	}
}
