.ml-iconbox {
	&:hover {
		.ml-iconbox__title--hasLine:after{
			width: 50px;
			background-color: #ff6666;
		}
	}
	@include md('center') {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	@include md('tab') {
		cursor: pointer;
	}
	@include md('l20') {
		padding-left: 20px;
		@include tablet_landscape {padding-left: 10px;}
	}
	@include el('iconWrapper') {
		margin-top: 27px;
	}
	@include el('icon') {
		font-size: 18px;
		color: #{$templateColor};
		@include md('left') {
			float: left;
			margin-right: 22px;
		}
		@include md('right') {
			float: right;
			margin-left: 22px;
		}
		@include md('img') {
			height: 60px;
			width: 60px;
		}
		@include md('mt-5') {margin-top: -5px;}
	}
	@include el('contentWrapper') {
		overflow: hidden;
	}
	@include el('titleWrapper') {
		margin-top: 25px;
	}
	@include el('title') {
		margin-top: 0px;
		margin-bottom: 0px;
		font-size: 14px;
		line-height: 15px;
		color: #{$textColor};
		@include md('hasLine') {
			&:after {
				content: '';
				position: relative;
				top: 15px;
				width: 30px;
				height: 3px;
				background: #{$mediumBlue};
				display: block;
				margin: 5px auto;
				transition: width .3s ease-in-out;
			}
		}
	}
	@include el('descWrapper') {
		margin-top: 0;
		margin-bottom: 14px;
	}
	@include el('desc') {
		font-size: 12px;
		color: #999999;
	}
	@include md('style1') {
		margin-top: 16px;
		margin-bottom: 16px;
		.ml-iconbox__iconWrapper {margin-top: 0px;}
		.ml-iconbox__icon {
			font-size: 50px;
			border: 2px solid #{$templateColor};
			border-radius: 50%;
			padding: 10px;
		}
		.ml-iconbox__title {
			font-size: 13px;
			font-weight: 400;
			padding-left: 10px;
			padding-right: 10px;
		}
		&:hover {
			.ml-iconbox__icon {
				transition: all 0.2s ease-in-out;
				background: #{$templateColor};
				color: #fff;
			}
			.ml-iconbox__title {
				transition: all 0.2s ease-in-out;
				color: #{$templateColor};
			}
		}
	}
	@include md('style2') {
		border-right: 1px solid #e5e5e5;
		padding-left: 15px;
		padding-right: 15px;
		padding-top: 40px;
		padding-bottom: 40px;
		@include max_desktop {
			padding-top: 10px;
			padding-bottom: 10px;
			border-right: none;
		}
		.ml-iconbox__icon {
			font-size: 50px;
			transition: all 0.4s ease-in-out;
		}
		.ml-iconbox__title {
			font-size: 18px;
			padding-bottom: 10px;
		}
		.ml-iconbox__desc {
			font-size: 14px;
			color: #{$textColor};
		}
		&:hover {
			.ml-iconbox__icon {
				color: #{$templateColor2};

			}
		}
	}
	@include md('style3') {
		.ml-iconbox__icon {
			font-size: 50px;
			transition: all 0.4s ease-in-out;
			@include md('left') {
				float: left;
				margin-right: 20px;
			}
		}
		.ml-iconbox__title {
			font-size: 30px;
			padding-bottom: 10px;
			color: #{$templateColor};
			transition: all 0.4s ease-in-out;
			&Wrapper {
				margin-top: 22px;
				margin-bottom: 30px;
			}
		}
		.ml-iconbox__desc {
			font-size: 16px;
			color: #000;
		}
		.ml-iconbox__contentWrapper {
			display: inline-block;
			text-align: center;
			min-width: 120px;
		}
		&:hover {
			.ml-iconbox__icon,
			.ml-iconbox__title  {
				color: #{$templateColor2};
			}
		}
	}
	@include md('style4') {
		padding-left: 40px;
		padding-right: 40px;
		padding-bottom: 45px;
		margin-top: 75px;
		margin-left: 10px;
		margin-right: 10px;
		border: 1px solid #e5e5e5;
		border-radius: 5px;
		.ml-iconbox__icon {
			font-size: 50px;
			transition: all 0.4s ease-in-out;
			background: #{$templateColor};
			color: #fff;
			border-radius: 50%;
			padding: 10px;
			&Wrapper {
				padding-bottom: 40px;
				margin-top: -22px;
				position: relative;
				z-index: 2;
			}
		}
		.ml-iconbox__title {
			color: #{$titleColor};
			font-size: 18px;
			padding-bottom: 30px;
		}
		.ml-iconbox__desc {
			font-size: 14px;
			color: #{$textColor};
		}
		&:hover {
			.ml-iconbox__icon {
				background: #{$templateColor2};
			}
		}
	}
	@include md('style5') {
		padding-left: 15px;
		padding-right: 15px;
		padding-top: 40px;
		padding-bottom: 50px;
		@include max_desktop {
			padding: 10px 0;
		}
		.ml-iconbox__icon {
			font-size: 50px;
			color: #fff;
		}
		.ml-iconbox__title {
			transition: all 0.4s ease-in-out;
			font-size: 16px;
			padding-bottom: 10px;
			color: #fff;
			opacity: .5;
			line-height: 20px;
			@include max_desktop {font-size: 12px;}
		}
		.ml-iconbox__desc {
			font-size: 14px;
			color: #fff;
		}
		.ml-iconbox__titleWrapper {margin-top: 5px;}
		&:hover {
			.ml-iconbox__title {opacity: 1;}
		}
	}
	@include md('style6') {
		.ml-iconbox__desc {
			font-size: 14px;
			color: #{$textColor};
		}
		.ml-iconbox__price {
			color: #252525;
			.ml-strike {text-decoration: line-through;}
		}
	}
}
