.ml-newsletter {
	margin-top: 10px;
	@include el('box') {
		padding: 40px 45px 65px;
		background: #{$templateColor};
		@include md('mb-60') {margin-bottom: 60px;}
	}
	@include el('button') {
		height: 40px;
		background-color: #{$templateColor};
		color: #fff;
		font-size: 14px;
		font-weight: 600;
		width: 40px;
		border-radius: 3px;
		float: right;
		border: 0;
		vertical-align: middle;
		outline: none;
		margin: 0;
		top: 0;
		span {left: -6px;}
	}
	input::placeholder {color: #d5dadf;}
}
// newsletter validation
/* newsletter validation */
.ml-newsletter-message.has-error {
	margin-top: 10px;
	background: #e63b42;
	color: #fff;
	padding: 10px 30px;
	line-height: 22px;
	border-radius: 3px;
}
.ml-newsletter-message.is-valid {
	margin-top: 10px;
	background: #3be679;
	color: #fff;
	padding: 10px 30px;
	line-height: 22px;
	border-radius: 3px;
}
.ml-newsletter-message.has-error,
.ml-newsletter-message.is-valid {
	@include mobile {margin-top: 90px;}
}
