/** 3.1 Homepage **/
.ml-iconBoxSection {
	border-bottom: 1px solid #e5e5e5;
	border-top: 1px solid #e5e5e5;
	.ml-iconbox__col:first-of-type .ml-iconbox {
		border-left: 1px solid #e5e5e5;
		@include max_desktop {border-left: none;}
	}

}
.ml-imgBoxSection {
	padding-top: 90px;
	padding-bottom: 125px;
	@include mobile {
		padding-top: 40px;
	}
}
// team sldier
.ml-teamSlider {
	background: #{$templateColor};

	@include min_tablet {
		height: 100%;
	}
	&--wrapper {
		height: 570px;
		position: relative;
		@include max_tablet {height: auto;}
		@include tablet_landscape {height: 400px;}
	}
	@include el('img') {
		background-size: cover;
		background-repeat: no-repeat;
		width: 100%;
		height: 570px;
		background-position: center;
		@include tablet_landscape {height: 400px;}
	}
	@include el('item') {
		width: 100%;
		height: 100%;
		&:focus {outline: none;}
	}
	.slick-list, .slick-track, .container,
	.row, .ml-teamSlider__caption, .col-xs-8 {height: 100%;}
	@include el('innerWrapper') {
		display: table;
		height: 570px;
		width: 100%;
		@include tablet_landscape {height: 400px;}
	}
	@include el('innerContent') {
		height: auto;
		display: table-cell;
		vertical-align: middle;
	}
	@include el('descBlock') {
		width: 50%;
		margin: auto;
		@include max_desktop {width: 80%;}
	}
	@include el('title') {
		font-size: 22px;
		margin-bottom: 0px;
		@include max_ldesktop {font-size: 13px;}
	}
	@include el('subtitle') {font-size: 12px; opacity: .5;}
	@include el('desc') {
		font-size: 18px;
		font-family: #{$titleFont};
		@include max_ldesktop {
			font-size: 12px;
			padding-top: 5px;
			padding-bottom: 0px;
		}
		@include md('pt-20') {padding-top: 20px;}
	}
	.slick-dots {
		bottom: 160px;
		left: 100px;
		@include tablet_landscape {bottom: 95px;}
		li {
			width: 14px;
			height: 14px;
			@include max_tablet {display: none;}
			button {
				border: 3px solid #fff;
				border-radius: 50%;
				width: 5px;
				height: 5px;
				padding: 5px;
				opacity: .5;
				&:before {display: none;}
			}
			&.slick-active button {opacity: 1;}
		}
	}
}
// counter section
.ml-counterSection {
	padding-top: 130px;
	padding-bottom: 140px;
	border-bottom: 1px solid #e5e5e5;
	@extend .ml-section;
	@include mobile {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	@include max_desktop {
		[class*=' col-']{
			padding-left: 80px;
			padding-right: 30px;
		}
		padding-top: 40px;
		padding-bottom: 40px;
	}
	@include tablet_portrait {
		[class*=' col-']{
			padding-left: 30%;
			padding-right: 30%;
		}
	}
	@include mobile {
		[class*=' col-']{
			padding-left: 20%;
			padding-right: 20%;
		}
	}
}

// services section
.ml-servicesSection {
	padding-top: 115px;
	padding-bottom: 175px;
	@include max_desktop {padding-bottom: 70px;}
	@include mobile {padding-top: 40px;}
}

// appointment section
.ml-appointment {
	&Section {@extend .ml-section;}
	@include el('box') {
		height: 425px;
		padding-left: 50px;
		padding-right: 50px;
		padding-top: 60px;
		@include md('style2') {
			height: auto;
			padding-top: 0px;
			.ml-appointment__title {
				text-align: left;
				margin-top: 0px;
				@include md('pb-30') {padding-bottom: 30px;}
			}
			.ml-appointment__submit {float: left;}
		}
		@include tablet_landscape {
			padding-left: 20px;
			padding-right: 20px;
		}
		@include max_tablet {height: auto;}
		@include mobile {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
	@include el('title') {
		text-align: center;
		color: #000;
		span {color: #{$templateColor};}
		@include md('light') {color: #fff;}
		@include md('regular') {font-weight: 400;}
		@include md('style2') {color: #{$titleColor2};}
		@include md('pb-20') {padding-bottom: 20px;}
		@include md('pb-30') {padding-bottom: 30px;}
	 }
	@include el('subtitle') {
		text-align: center;
		@include md('pb-20') {padding-bottom: 20px;}
	}
	@include el('text') {
		font-family: #{$titleFont};
		margin-bottom: 50px;
	}
	@include el('submit') {
		margin: auto;
		display: block;
		&:hover {
			background: #5fbff7;
		}
		&Wrapper {
			width: 100%;
			margin-top: 40px;
			padding-bottom: 40px;
		}
		&:focus {outline: none;}
	}
	@include el('bgImg') {
		background-image: url('../images/appointment.jpg');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: auto;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		&Wrapper {
			height: 425px;
		}
	}
}
.ml-programList {
	padding-top: 10px;
	padding-bottom: 40px;
	margin-bottom: 0px;
	@include el('item') {
		border-bottom: 1px solid #e5e5e5;
		padding-bottom: 5px;
		padding-top: 5px;
		span {float: right;}
		@include tablet_landscape {font-size: 11px;}
	}
	@include md('style1') {
		li {
			border-top: 1px solid rgba(0,0,0,0.07);
			border-bottom: none;
			color: #fff;
		}
	}
}
// categories section
.ml-categories__title {
	color: #e5e5e5;
	font-weight: 400;
	padding-bottom: 20px;
