//map slider
.ml-mapSlider {
	@include el('wrapper') {
		max-height: 570px;
		position: relative;
		height: 500px;
		@include max_desktop {max-height: 800px; height: auto;}
	}
	@include el('info') {
		background: #f3f3f3;
	}
	.slick-arrow {
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		cursor: pointer;
		outline: none;
		font-size: 40px;
		@include max_tablet {
			font-size: 30px;
			top: 45%;
		}
		&.ml-icon-arrow-left {
			left: -60px;
			@include max_tablet {
				left: auto;
				z-index: 1;
				right: 80px;
			}
		}
		&.ml-icon-arrow-right {
			right: -60px;
			@include max_tablet {
				right: 25px;
			}
		}
	}
	.slick-dots {
		bottom: 30px;
		left: 0px;
		li {
			width: 8px;
			height: 8px;
			@include max_tablet {bottom: 30px;}
			button {
				border: 2px solid #ccc;
				border-radius: 50%;
				width: 5px;
				height: 5px;
				padding: 4px;
				&:before {display: none;}
			}
			&.slick-active button {border-color: #{$blue};}
		}
	}
}
.ml-mapDetails {
	background: #fff;
	height: 500px;
	padding: 60px 50px 90px;
	@include tablet_landscape {padding: 30px 50px 90px;}
	@include el('city') {
		color: #{$templateColor};
		font-family: #{$titleFont};
	}
	@include el('chamber') {margin-top: 0px;}
	@include el('address') {margin-bottom: 0px;}
	@include el('status') {
		color: #66cc99;
		font-size: 12px;
		padding-bottom: 20px;
	}
	@include el('estimated') {
		font-size: 16px; font-weight: bold;
		span {
			font-size: 14px; font-weight: 400;
			&:after {
				content:"•";
				color: #66cc99;
				font-size: 35px;
				vertical-align: bottom;
			}
		}
	}
	@include el('program') {
		font-size: 13px;
		font-weight: bold;
		span {font-weight: 400;}
	}
	@include el('btn') {
		margin-top: 20px;
		display: inline-block;
		padding: 8px 20px;
	}
}
.ml-mapServices {
	background: #fff;
	height: 500px;
	padding: 110px 40px 90px;
	@include max_tablet {display: none;}
	@include el('title') {
		font-size: 14px;
		padding-bottom: 15px;
	}
	&-list {
		@include el('item') {
			padding-bottom: 5px;
			&:hover a{color: #66cc99;}
		}
	}
}
