	@include mobile {height: 500px;}
	@include el('wrapper') {
		padding: 140px 100px 140px 270px;
		@include tablet_landscape {padding: 95px 25px 0px 245px;}
		@include max_tablet {padding: 140px 50px 50px 50px;}
		@include mobile {    padding: 70px 30px 50px 30px;}
	}
	@include el('subtitle') {
		font-size: 16px;
		color: #{$servicesTextColor};
		font-weight: 700;
	}
