		font-weight: 700;
		letter-spacing: 2px;
		font-size: 10px;
		span {padding-left: 5px;}
	}
	@include el('viewAll') {
		color: #000;
		font-weight: 700;
		letter-spacing: 2px;
		font-size: 10px;
		span {padding-left: 5px;}
		position: absolute;
		bottom: 50px;
		left: 15px;
		@include max_tablet {display: none;}
		&-sm {
			@include max_tablet {display: block;}
			@include min_tablet {display: none;}
