			left: 50%;
			width: 90%;
		}
		@include tablet_landscape {
			left: 108%;
			width: 400px;
		}
		@include desktop {
			left: 94%;
			width: 430px;
		}
		@include el('wrapper') {
			height: 570px;
			@include max_tablet {height: 400px;}
			@include mobile {height: 300px;}
		}
	}
}
