.ml-gridGallery {
	position: relative;
	margin-bottom: 80px;
}
.ml-gridItem {
	&-link {
		position: relative;
		display: block;
		/* grid zooming animation */
		&:hover .ml-icon {
			display: block;
			opacity: 1;
			margin-top: 0;
		}
		&.ml-zoom--on {
			.ml-gridImg {
				transition-duration: 2s;
				transition: transform 0.7s linear,-webkit-transform 0.7s linear;
			}
			&:hover .ml-gridImg {
				transform: scale(1.1);
			}
		}
	}
	@include el('title') {
		font-size: 36px;
		color: #{$templateColor};
		text-align: center;
		margin-bottom: 0px;
		padding-bottom: 20px;
		@include md('style2') {
			font-size: 22px;
		}
		@include md('plr-50') {
			padding-left: 50px;
			padding-right: 50px;
		}
	}
	@include el('details') {
		font-size: 12px;
		color: #{$servicesTextColor};
		padding-bottom: 15px;
		@include md('pt-20') {padding-top: 20px;}
	}
	@include el('author') {
		color: #{$titleColor};
	}
	@include el('description') {
		color: #{$textColor};
		padding-top: 25px;
		padding-bottom: 50px;
		margin-bottom: 0px;
		padding-left: 15px;
		padding-right: 15px;
	}
	@include md('w50') {
		width: 50%;
	}
}

 /* grid search icon */
.ml-circled-icon {
	width: 56px;
	height: 56px;
	line-height: 56px;
	display: inline-block;
	border-radius: 50%;
	box-sizing: content-box;
	text-align: center;
	border: 2px solid currentColor;
	&:before {
		line-height: 56px;
		display: block;
		vertical-align: middle;
	}
}
.ml-icon {
	opacity: 0;
	position: absolute;
	top: 50%;
	margin-top: 15px;
	left: 50%;
	transform: translate(-50%,-50%);
	transition: all .25s ease-in-out;
	border-color: #fff;
	color: #fff;
}

.ml-gridImg {
	&-wrapper {
		overflow: hidden;
	}
}





