.ml-sidebar {
	width: 100%;
	@include md('w310') {width: 310px;}
	@include max_desktop {width: 100%;}
	@include max_tablet {width: 100%;}
	@include md('right') {float: right;}
	@include el('container') {
		padding-left: 30px;
		padding-right: 30px;
	}
	@include el('title') {
		color: #ccc;
		font-size: 14px;
		font-weight: 700;
		font-family: #{$titleFont};
		@include md('transparent') {
			opacity: .5;
		}
		@include md('pb-20') {padding-bottom: 20px;}
		@include md('pb-25') {padding-bottom: 25px;}
		@include md('style2') {
			font-size: 16px;
			margin-top: 40px;
			color: #252525;
		}
	}
	@include el('iconbox') {
		padding: 45px 55px;
		background: #fff;
		margin-bottom: 60px;
		@include tablet_portrait {
			padding: 45px 185px;
		}
	}
	@include md('mt-20') {margin-top: 20px;}
}
