.ml-testimonial {
	padding-top: 35px;
	padding-bottom: 25px;
	@include el('img') {
		width: 100px;
		height: 100px;
		@include mobile {display: block; margin: auto;}
		&Wrapper {
			float: left;
			@include mobile {
				float: none;
				display: block;
				margin-bottom: 20px
			}
		}
	}
	@include el('textContainer') {
		overflow: hidden;
		padding-left: 30px;
	}
	@include el('title') {
		font-weight: 700;
		color: #000;
		margin-bottom: 0px;
	}
	@include el('text') {margin-bottom: 15px;}
	@include el('link') {
		color: #{$templateColor};
		font-style: italic;
		font-size: 12px;
	}
}
