/*** form element ***/

/* text field */
button, input, optgroup, select, textarea {
	font-family: sans-serif;
}
input[type="password"]:focus,
input[type="text"]:focus,
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus,
input[type="email"],
textarea:focus {
	outline: none;
}
input {color: #555; border-bottom: 1px solid #{$templateColor};}
input[type="password"],
input[type="text"],
input[type="file"],
input[type="email"],
textarea {
	max-width: 100%;
	margin-bottom: 15px;
	padding: 18px 10px 6px 5px;
	height: auto;
	background-color: transparent;
	box-shadow: none;
	border-width: 0 0 1px;
	border-style: solid;
	border-radius: 0;
	display: block;
	width: 100%;
	font-size: 14px;
	line-height: 1.42857143;
	background-image: none;
	border-color: ease-in-out .15s, box-shadow ease-in-out .15s;
	z-index: 1;
	position: relative;
	resize: none;
	&::placeholder {color: #555;}
	&.ml-newsletter__input {
		padding: 6px 12px;
		font-size: 14px;
		line-height: 1.42857143;
		height: 40px;
		width: calc(100% - 50px);
		margin-right: 0px;
		background: #445361;
		padding-left: 20px;
		transition: padding-left .15s ease-out;
		font-family: sans-serif;
		border: none;
		color: #d5dadf;
		&::placeholder {color: #d5dadf;}
		display: inline-block;
		&:focus {
			padding-left: 30px;
			outline: none;
		}
	}
}

textarea {
	&.ml-textarea--style2 {
		margin-top: 20px;
		height: 80px;
	}
}

input:focus, textarea:focus {
	border-bottom-width: 1px;
	border-color: #{$hoverColor};
}
input[type="text"]:focus+label,
input[type="password"]:focus+label,
input[type="email"]:focus+label,
.ml-input[class~="hasContent"]+label {
	top: -10px;
	font-size: 10px;
	font-weight: 700;
	&.ml-elem--center {
		top: 10%;
	}
}
input[type="text"]+label,
input[type="password"]+label,
input[type="email"]+label{
	font-size: 14px;
	position: absolute;
	top: 15px;
	left: 5px;
	cursor: text;
	font-weight: 400;
	transition: all .25s ease;
	&.ml-elem--center {
		top: 50%;
		left: 50%;
	}
}
label {color: #{$hoverColor}; }
.ml-form-component {
	margin-bottom: 5px;
	position: relative;
}
.ml-form-component {
	opacity: 1;
	transition: opacity .5;
	position: relative;
}
.ml-form-component--hidded {opacity: 0; position: absolute;}
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
	text-shadow: none;
	padding: 10px 20px;
	line-height: 1.6;
	box-shadow: none;
	font-size: 13px;
	font-weight: 700;
	border-style: solid;
	color: #fff;
	border-width: 0;
	transition: background-color .15s ease-out;
	background-color: #{$templateColor};
}
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
button:hover {
	background: #{$hoverColor};
}
select {
	padding: 10px;
	border-radius: 5px;
}
table, th, tr, td {
	border: 1px solid #000;
}
th, tr, td {padding: 10px;}
input[type="radio"],
input[type="checkbox"] {
	display: inline;
}

/* form components styles */
.ml-component--light {
	label {color: #fff;}
	input, textarea {
		border-color: #fff;
		color: #fff;
	}
	input:-webkit-autofill,
	input:-webkit-autofill:hover,
	input:-webkit-autofill:focus,
	input:-webkit-autofill:active {
			transition: background-color 5000s ease-in-out 0s;
			-webkit-text-fill-color: #fff !important;
	}
	input:-webkit-autofill:focus + label {display: none;}
}
.ml-component--dark {
	label {color: #333;}
	input, textarea {border-color: #333;}
}
.ml-component--grey {
	label {color: #333;}
	input, textarea {border-color: #ccc;}
}
.ml-component--style1 {
	margin-bottom: 10px;
	label {color: #{$footerTitlesColor};}
	input, textarea {
		border-color: #ccc;
		transition: all 0.5s;
	}
	input:focus {border-color: #{$templateColor};}
	input[type="password"],
	input[type="text"],
	input[type="file"] {
		margin-bottom: 0px;
		padding: 10px 40px 10px;
	}
	input[type="text"]:focus+label,
	input[type="password"]:focus+label,
	input[type="email"]:focus+label,
	.ml-input[class~="hasContent"]+label {
		top: -5px;
	}
	input[type="text"]+label,
	input[type="password"]+label,
	input[type="email"]+label {
		top: 12px;
		left: 35px;
	}
}
.js-disable-action {pointer-events: none;}
.js-cf-message {
		display: none;
		border-left: solid 3px transparent;
		padding-left: 5px;
}
.js-response-success {border-left-color: #9bf442 ;}
.js-response-error {border-left-color: #dd0000 ;}
.ml-form-icon {
	position: absolute;
	top: 16px;
	left: 10px;
	font-size: 16px;
}
.ml-select {
	border-top: none;
	border-left: none;
	border-right: none;
	border-radius: 0px;
	padding-left: 5px;
	border-bottom: 2px solid #ccc;
	font-size: 14px;
	font-family: #{$defaultFont};
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	width: 255px;
	padding-bottom: 5px;
	margin-top: 20px;
	background: transparent url("../img-assets/arrow-down.png") no-repeat 95%;
	cursor: pointer;
	&:focus {outline: none;}
	@include md('style2') {
		padding-bottom: 6px;
		margin-top: 5px;
		border-bottom: 1px solid #ccc;
		width: 100%;
	}
	@include md('style3') {
		border: 1px solid #ccc;
		display: inline-block;
		position: relative;
		right: 0px;
		width: 72%;
		padding-left: 20px;
		background: transparent url("../img-assets/arrow-down3.png") no-repeat 95%;
	}
}

.ml-label--style1 {
	color: #000;
	font-family: #{$titleFont};
	span {color: red;}
	width: 25%;
}

.input-group-addon {
	&.input-group-addon--style2 {
		background: none;
		border-radius: 0px;
		border-top: none;
		border-right: none;
		vertical-align: bottom;
		padding: 9px 12px 8px 12px;
		font-size: 16px;
	}
}
.form-control:focus {
	box-shadow: none;
	border-color: #ccc;
}

input[type="text"].ml-input {
	@include md('style2') {
		border: 1px solid #ccc;
		border-width: 1px 1px 1px;
		margin-bottom: 0px;
		height: 40px;
		padding: 0px;
		&:focus {border: 1px solid #ccc;}
	}
}
