.ml-verticalTabs {
	min-height: 380px;
	@include md('ml30') {margin-left: 30px; @include mobile {margin-left: 0px;}}
	@include md('pl30') {padding-left: 20px; @include mobile {padding-left: 0px}}
	@include el('list') {
		@include md('centered') {
			@include min_ldesktop {
				width: 70%;
				margin: auto;
			}
		}
		border-bottom: 1px solid #e5e5e5;
		li {
			display: inline-block;
			padding: 25px;
			color: #{$servicesTextColor};
			&.activeTab {
				color: #{$templateColor};
				border-bottom: 1px solid #{$blue};
			}
			&:hover {
				cursor: pointer;
			}
		}
	}
	@include el('panels') {
		padding-top: 25px;
		.ml-panel {
			display: none;
			&.activeTab {display: block;}
			@include el('title') {
				font-weight: 400;
				margin-top: 0px;
				padding-bottom: 10px;
				color: #{$templateColor};
			}
			@include el('text') {
				color: #{$textColor};
			}
		}
	}
	@include md('style2') {
		@include max_tablet {
			padding-left: 20px;
			padding-right: 20px;
		}
		.ml-tabs__list {
			border-bottom: none;
			li {
				padding: 15px;
				font-size: 12px;
				color: #000;
				opacity: .4;
				transition: all .2s;
				font-weight: 700;
				text-transform: uppercase;
				position: relative;
				&.activeTab {
					border: none;
					opacity: 1;
					&:after {
						background: #000;
						height: 2px;
						position: absolute;
						bottom: 0;
						content: "";
						width: 100%;
						left: 0px;
					}
				}
			}
		}
		.ml-verticalTabs__panels {
			padding-top: 45px;
		}
		.ml-panel {
			p {color: #{$textColor};}
			ul {
				padding-left: 15px;
				list-style-type: disc;
				color: #{$textColor};
				li {
					font-size: 60%;
					span {font-size: 166%;}
				}
			}
		}
	}
}

.ml-panel {
	visibility: hidden;
	overflow: hidden;
	display: block;
	height: 0;
	&.activeTab {
		animation-duration: 1000ms;
		animation-name: fadeIn;
		animation-delay: 0ms;
		overflow: visible;
		visibility: visible;
		height: auto;
	}
}
