// header dark style
.ml-header {
	position: relative;
	z-index: 2;
	@include md('dark') {
		.ml-topbar-wrapper, .ml-btn {background: #{$hoverColor};}
		.ml-btn {&:hover {background: #{$hoverColor2};}}
		.ml-iconbox__icon {color: #{$blue};}
		.ml-headerInfo .ml-iconbox__title {color: #fff;}
		.ml-logobar-wrapper, .ml-menubar-wrapper {background: #{$darkBlue};}
		.ml-logobar-wrapper {border-bottom: 1px solid #343241;}
		.ml-mainMenu__item.active >a {color: #fff; font-weight: 700;}
		.ml-mainMenu__item >a {color: #{$blue};}
		.ml-searchBtn span {color: #fff;}
		.ml-btn--white {
			color: #000; background: #fff;
			&:hover {
				background: #f8f7f7;
			}
		}
	}
}
.ml-topbar-wrapper {
	background: #{$templateColor};
}
.ml-headerbar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	@include md('block-xs') {
		@include mobile {
			display: block;
		}
	}
	@include md('block-sm') {
		@include max_tablet {
			display: block;
		}
	}
}
.ml-topbar {height: 45px;}
.ml-cta-text {
	display: inline-block;
	line-height: 15px;
	@include max_tablet {display: none;}
}
.ml-cta-btn {
	display: inline-block;
	padding: 3px 10px;
	margin-left: 10px;
	font-family: #{$titleFont};
	font-weight: bold;
}
// logobar
.ml-logobar-wrapper {background: #fff; border-bottom: 1px solid #E4E4E4;}
.ml-logobar {
	height: 85px;
	@include max_tablet {height: auto;}
}
.ml-logoContainer {
	position: relative;
	display: block;
	margin-top: 5px;
	@include max_tablet {width: 100%;}
	@include mobile {height: 20vh;}
}
// header logo
.ml-logoImg {
	margin: auto;
	height: auto;
	width: 150px;
	@include mobile {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		width: 50%;
		height: auto;
		margin: auto;
	}
}
.ml-headerInfo {
	width: 50%;
	@include tablet_landscape {width: 60%;}
	@include tablet_portrait {width: auto;}
	@include mobile {display: none;} // hide header info on mobile
}
.ml-menubar-wrapper {
	background: #fff;
	border-bottom: 1px solid #E4E4E4;
}
.ml-menubar {
	height: 60px;
}
// 2.1. Navigation
// Main menu
.ml-mainMenu {
	&-trigger {
		width: 20px;
		vertical-align: middle;
		position: relative;
		display: inline-block;
		height: 16px;
		margin-right: 10px;
		transform: rotate(0deg);
		transition: .2s ease-out;
		@include min_tablet {display: none;}
		cursor: pointer;
		opacity: .8;
		a {display: block; }
		span {
			height: 3px;
			background: #{$elementsTextColor};
			display: block;
			position: absolute;
			width: 100%;
			border-radius: 2px;
			opacity: 1;
			left: 0;
			transform: rotate(0deg);
			transition: .25s ease-in-out;
			&:nth-child(1) {top: 0;}
			&:nth-child(2) {top: 6px;}
			&:nth-child(3) {top: 12px;}
		}
	}
	height: 100%;
	@include max_tablet {display: none;}
	@include el('item') {
		display: table;
		float: left;
		padding: 0 16px;
		font-size: 15px;
		height: 100%;
		vertical-align: middle;
		margin-top: 2px;
		&:first-of-type {
			padding-left: 0px;
		}
		@include max_ldesktop {
			padding-right: 15px;
			font-size: 12px;
		}
		@include max_desktop {
			padding-right: 8px;
			font-size: 12px;
			padding: 0px 8px;
		}
		> a {
			display: table-cell;
			vertical-align: middle;
		}
		&.active {
			a {color: #{$templateColor};}
		}
		&.has-submenu {
			position: relative;
			padding-right: 0px;
			&:after {
				font-family: "Glyphicons Halflings";
				content:"\e114";
				font-size: 7px;
				padding-left: 11px;
				display: table-cell;
				vertical-align: middle;
				position: relative;
				color: #{$LinksColor};
			}
			&:hover {
				.ml-megamenu {
					left: -75px;
					opacity: 1;
					top: 100%;
					visibility: visible;
					z-index: 100;
					top: 40px;
					@include mobile {display: none;}
					@include max_ldesktop {left: -40px;}
					@include md('left135') {
						left: -135px;
						@include tablet_landscape {left: -55px;}
					}
					@include md('left235') {
						left: -235px;
						@include desktop {left: -190px;}
						@include tablet_landscape {left: -160px;}
					}
					@include md('left340') {
						left: -340px;
						@include desktop {left: -190px;}
						@include tablet_landscape {left: -160px;}
					}
				}
				.ml-submenu {
					left: -70px;
					opacity: 1;
					top: 100%;
					visibility: visible;
					z-index: 100;
					top: 40px;
					right: auto;
				}
			}
		}
	}
}

.ml-submenu {
	top: 100%;
	transform: translateY(10px);
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease,top .45s ease, transform .45s;
	position: absolute;
	border-radius: 2px;
	border: 1px solid rgba(255, 255, 255, 0.8);
	box-shadow: 0px 14px 32px 0px rgba(0, 0, 0, 0.14);
	background: #fff;
	right: -5000px;
	min-width: 230px;
	padding: 15px 30px 20px 40px;
	line-height: 2.6;
	width: 310px;
	ul {
		float: left;
		&:not(:first-child) {
			padding-left: 20px;
		}
	}
	@include el('item') {
		font-size: 13px;
		font-family: #{$titleFont};
		a {
			min-width: 230px;
			color: #{$textColor};
			&:hover {color: #{$templateColor};}
		}
	}
}

.ml-megamenu {
	top: 100%;
	transform: translateY(10px);
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease,top .45s ease, transform .45s;
	position: absolute;
	border-radius: 2px;
	border: 1px solid rgba(255, 255, 255, 0.8);
	box-shadow: 0px 14px 32px 0px rgba(0, 0, 0, 0.14);
	background: #fff;
	min-height: 210px;
	right: -5000px;
	padding: 10px 30px 10px 40px;
	width: 1130px;
	@include max_ldesktop {width: 800px; padding-bottom: 20px;}
	@include tablet_landscape {
		width: 700px;
		padding-left: 0px;
		padding-right: 0px;
	}
	@include el('container') {
		position: relative;
		padding-left: 15px;
		padding-right: 15px;
		width: 100%;
		height: 100%;
		@include md('style2') {
			padding-bottom: 26px;
			padding-top: 30px;
		}
		@include md('style3') {
			padding-bottom: 25px;
			padding-top: 25px;
		}
	}
	@include el('Img') {
		position: relative;
		bottom: -10px;
	}
	@include el('list') {
		display: inline-block;
		padding-top: 5px;
		padding-bottom: 5px;
		&Item {
			position: relative;
			a {
				display: block;
				font-size: 13px;
				line-height: 22px;
				font-weight: 700;
				color: #686868;
				font-style: normal;
				position: relative;
				padding: 10px 15px;
				transition: padding-left .35s ease,border-right .35s ease;
				font-weight: 400;
				font-family: #{$titleFont};
				@include desktop {font-size: 11px;}
				@include tablet_landscape {font-size: 10px;}
				&:after {
					position: absolute;
					content: '';
					width: 95%;
					background: rgba(0, 0, 0, 0.03);
					height: 1px;
					left: 0px;
					bottom: 0px;
				}
			}
			&:hover a{color: #{$templateColor};}
		}
	}
	@include el('component') {
		display: inline-grid;
		margin-left: -5px;
		@include md('right') {
			float: right;
		}
		@include md('ptb-15') {
			padding-top: 15px;
			padding-bottom: 15px;
		}
		@include md('pr-20') {
			padding-right: 20px;
		}
	}
	@include el('footer') {
		position: relative;
		bottom: 0px;
		padding-bottom: 10px;
		&Text {
			font-weight: 400;
			font-size: 21px;
			margin-top: 30px;
			margin-left: 13px;
			margin-right: 25px;
			display: inline-block;
			color: #{$templateColor};
		}
		&Btn {
			display: inline-block;
			font-size: 13px;
			@include max_ldesktop {margin-left: 20px;}
		}
	}
	@include el('title') {
		font-size: 16px;
		font-weight: 700;
		margin-bottom: 20px;
		color: #000;
	}
}
.ml-megamenu[data-col-nr="5lg-4md"] {
	.ml-megamenu__component {
		width: 20%;
		@include max_ldesktop {width: 25%;}
	}
}
.ml-megamenu[data-col-nr="5"] {
	.ml-megamenu__component {
		width: 20%;
	}
}
.ml-megamenu[data-col-nr="4"] {
	.ml-megamenu__component {width: 25%;}
}
.ml-servicesList {
	line-height: 2.6;
	@include el('item') {
		font-size: 13px;
		font-family: #{$titleFont};
		a {color: #{$textColor};}
	}
}

.ml-searchBar {
	min-width: 100px;
	float: right;
}

// cart button
.ml-cart {
	position: relative;
	z-index: 2;
	width: 30px;
	height: 30px;
	margin-right: 10px;
	float: right;
	right: 0;
	@include el('icon') {
		font-size: 20px;
		color: #ff6666;
	}
	@include el('link') {
		width: 100%;
		display: block;
	}
	&:hover .ml-infoCart{
		right: -20px;
		opacity: 1;
		top: 100%;
		visibility: visible;
		z-index: 100;
		top: 20px;
		@include mobile {right: -39px;}
	}
	.ml-infoCart {
		top: 100%;
		transform: translateY(10px);
		opacity: 0;
		visibility: hidden;
		transition: opacity .4s ease,top .45s ease, transform .45s;
		position: absolute;
		border-radius: 2px;
		border: 1px solid rgba(255, 255, 255, 0.8);
		box-shadow: 0px 14px 32px 0px rgba(0, 0, 0, 0.14);
		padding: 30px 40px 40px;
		background: #fff;
		width: 300px;
		min-height: 210px;
		right: -5000px;
		max-height: 600px;
		&-btn {
			display: inline-block;
			font-size: 11px;
		}
		&-buttons {
			display: block;
			margin-top: 15px;
		}
	}


	.ml-cartList {
		@include el('item') {
			padding-top: 2px;
			padding-bottom: 2px;
			&Title {
				display: inline-block;
				font-size: 12px;
				font-weight: 400;
				width: 65%;
				margin-top: 0px;
				margin-bottom: 0px;
				color: #{$elementsTextColor};
			}
			&Img {
				position: relative;
				margin-bottom: 25px;
				width: 45px;
				height: 45px;
				top: 0px;
			}
		}
		@include el('Total') {
			margin-top: 15px;
			margin-top: 15px;
			font-size: 10px;
			font-weight: 700;
			display: inline;
		}
		@include el('Price') {
			font-size: 11px;
			float: right;
			padding-right: 5px;
			font-family: #{$titleFont};
			color: #{$templateColor};
		}
		@include el('itemRemove') {
			font-size: 8px;
			padding-left: 15px;
			top: -10px;
			cursor: pointer;
			color: #{$textColor};
		}

	}
}

/* search button */
.ml-search {
	float: right;
	position: relative;
	.ml-search-container {
		width: 290px;
		margin-left: 10px;
		position: absolute;
		right: 0;
		padding: 5px 20px;
		background: #fff;
		border-bottom: 1px solid #fff;
		z-index: 11;
		box-shadow: 0px 14px 32px 0px rgba(0, 0, 0, 0.14);
		display: none;
		&.panel-opened {display: block;}
	}
	@include el('input[type="text"]') {
		width: auto;
		display: inline-block;
	}
	@include el('submit') {
		display: inline-block;
		background: #fff;
		color: #{$templateColor};
		&:hover {
			background: #fff;
			color: #{$hoverColor};
		}
		&:focus {outline: none;}
	}
}

// Responsive menu
.ml-resMenu {
	padding: 0;
	margin: 0;
	list-style: none;
	right: 0;
	top: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	background: #fff;
	overflow: scroll;
	backface-visibility: hidden;
	transition: transform .5s ease;
	z-index: 10000;
	transform: translateX(100%);
	transform-style: preserve-3d;
	@include min_mobile {
		max-width: 400px;
	}
	li {
		backface-visibility: hidden;
		border-color: rgba(0, 0, 0, 0.15);
		position: static;
		border-bottom: 1px solid rgba(0,0,0,.15);
		&.ml-resMenu-back {
			.ml-resMenu-backLink {
				font-size: 14px;
				padding: 12px 28px 12px;
				display: block;
				font-weight: 400;
				cursor: pointer;
				color: rgba(0,0,0,.45);
				text-align: center;
				text-transform: capitalize;
				&:hover {
					color: rgba(0, 0, 0, 0.85);
					text-decoration: none;
				}
			}
			.ml-resMenu-backIcon {
				left: 0;
				right: auto;
				border-left: none;
				border-right: 1px solid rgba(0, 0, 0, 0.15);
				position: absolute;
				width: 49px;
				height: 45px;
				line-height: 45px;
				vertical-align: middle;
				text-align: center;
				cursor: pointer;
				color: rgba(0, 0, 0, 0.45);
				top: 0;
			}
		}
		a {
			padding: 12px 28px 12px;
			display: block;
			font-weight: 400;
			color: rgba(0,0,0,.45);
			text-transform: uppercase;
			font-size: 13px;
			&.active {
				background-color: rgba(0,0,0,.05);
				color: rgba(0,0,0,.85);
			}
			&:hover {color: rgba(0,0,0,.85);}
		}
	}
	/* rez submenu */
	.ml-res-submenu {
		.ml-res-submenu-trigger {
			position: absolute;
			right: 0;
			width: 49px;
			height: 45px;
			border-left: 1px solid rgba(0,0,0,.15);
			line-height: 45px;
			vertical-align: middle;
			text-align: center;
			cursor: pointer;
			color: rgba(0,0,0,.45);
			top: auto;
		}
		> ul {
			padding: 0;
			margin: 0;
			list-style: none;
			right: 0;
			top: 0;
			position: absolute;
			width: 100%;
			height: 100%;
			background: #fff;
			overflow-y: scroll;
			backface-visibility: hidden;
			transition: -webkit-transform .5s ease;
			transition: transform .5s ease;
			z-index: 10000;
			transform: translateX(100%);
			transform-style: preserve-3d;
			ul {
				border: 1px solid rgba(0,0,0,.15);
			}
			&.ml-menu--visible {
				display: block;
				transform: translateZ(0);
			}
		}
	}
	&.ml-menu--visible {
		overflow: hidden;
		transform: translateZ(0);
		display: block;
		@include min_tablet {display: none;}
	}
}

// subheader
.ml-subheader {
	height: 370px;
	position: relative;
	overflow: hidden;
	top: 0;
	z-index: 0;
	width: 100%;
	@include el('btn') {
		width: 230px;
		display: block;
		margin:auto;
	}
	@include md('h590') {
		height: 590px;
	}
	@include el('img') {
		width: auto;
		height: auto;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 0;
	}
	@include el('title') {
		font-size: 38px;
		font-weight: 700;
		display: inline-block;
		color: #{$templateColor};
		position: relative;
		left: 0px;
		z-index: 2;
		margin-bottom: 0px;
		width: 100%;
		height: auto;
		@include md('top') {
			@include max_tablet {
				width: 100%;
				position: relative;
				top: -130px;
				text-align: center;
			}
		}
		@include md('mg-tablet') {
			@include tablet_portrait {margin-bottom: 100px;}
		}
		@include md('has-bookpanel') {
			@include min_tablet {margin-right: 350px;}
		}
		@include max_tablet {font-size: 30px;}
	}
	@include el('subtitle') {
		font-size: 15px;
		font-weight: 700;
		font-family: #{$titleFont};
		position: relative;
		z-index: 2;
		color: #{$servicesTextColor};
		@include md('pb-30') {
			padding-bottom: 30px;
		}
		@include md('center') {
			display: inline-block;
			max-width: 310px;
		}
	}
	@include el('content') {
		@include md('center') {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 100%;
			z-index: 9;
		}
	}
}

.ml-bookPanel {
	width: 310px;
	height: 200px;
	background: #fff;
	transition: height .5s;
	overflow: hidden;
	@include md('expanded') {
		height: 250px;
	}
	@include mobile {width: 290px;}
	@include el('title') {
		color: #fff;
		&Wrapp {
			background: #{$templateColor};
			padding: 5px 30px;
		}
	}
	@include el('form') {
		padding: 5px 30px;
		&Submit {
			span {
				font-size: 18px;
				margin-right: 5px;
				vertical-align: sub;
			}
		}
	}
}
