/** 4. Footer  */

.ml-footer {
	background: #4f6070;
	padding-top: 120px;
	padding-bottom: 50px;
	@extend .ml-section;
	@include max_tablet {padding-left: 20px;}
	&Title {
		color: #{$footerTitlesColor};
		font-size: 11px;
		font-weight: 700;
		@include md('pb-10') {
			padding-bottom: 10px;
		}
	}
	&Text {
		color: #{$footerTextColor};
	}
	&List {
		padding-top: 15px;
		@include el('item') {
			font-size: 14px;
			padding-bottom: 8px;
			a{
				color: #{$footerTextColor};
				&:hover {color:#{$templateColor};}
			}
		}
	}
}
.ml-copyright {
	color: #{$footerTitlesColor};
	opacity: .5;
	text-align: center;
	span {color: #fff;}
}
