.ml-imageBox {
	opacity: .9;
	transition: all 0.4s ease-in-out;
	@include md('bordered') {border: 1px solid #e5e5e5;}
	@include md('circle') {img {border-radius: 50%;}}
	@include md('w70') {img {width: 70px;}}
	@include md('left') {
		.ml-imageBox-imgWrapper  {
			float: left;
			top: 30px;
		}
		.ml-imageBox-details {overflow: hidden;}
	}
	@include md('detailsHover') {
		.ml-imageBox-details {background: #fbfbfb;}
		&:hover {
			.ml-imageBox-details {
				background-color: #fff;
			}
		}
	}
	@include md('hasShadow') {
		&:hover {box-shadow: 1px 1px 50px 1px #ccc;}
	}
	@include md('mb-30') {margin-bottom: 30px;}
	@include md('mb-40') {margin-bottom: 40px;}
	&-wrapper {
		position: relative;
		display: block;
		.ml-imageBox-imgWrapper {
			display: block;
			position: relative;
			img {
				display: block;
				margin: 0 auto;
				&.ml-imgWidth--auto {
					width: auto;
				}
			}
		}
		p {font-size: 13px;}
	}
	&:hover {opacity: 1;}
}
.ml-imageBox-details {
	background-color: #fff;
	padding-top: 20px;
	padding-right: 20px;
	padding-bottom: 20px;
	padding-left: 20px;
	.ml-imageBox-title {
		font-size: 16px;
		line-height: 26px;
		font-weight: 700;
		color: #0a0a0a;
		@include md('style1') {
			font-size: 14px;
			margin-bottom: 0px;
			color: #{$textColor};
		}
	}
	.ml-imageBox-subtitle {
		font-size: 14px;
		line-height: 26px;
		font-weight: 400;
		color: #535353;
		font-family: #{$defaultFont};
		span {color: #{$templateColor};}
	}
}
